<template>
	<view >
    <ReturnHead title="我的" complete=""></ReturnHead>
	<scroll-view scroll-y="true" class="scrollY">
	<profile-head 	
	 :he="detail"
	 :detail="true"></profile-head>
	<pr-index-floor
	  v-for="(item, index) in follow.list"
	  :list="item"
	  @click.native="forPostDetail(item.articleId, item.tagList)"
	  @unlikeIt="unlikeIt"
	  @likeIt="likeIt"
	></pr-index-floor>
	<!-- 上拉加载更多开始 -->
	  <view>
	    <load-more :loadStatus="loadStatus"></load-more>
	  </view>
	  <!-- 上拉加载更多结束 -->
	</scroll-view>
	<!-- 点击更多后产生的的上拉菜单开始 -->
	<uni-popup ref="popup" :mask-click="false">
	  <popup-bottom @close="close" />
	</uni-popup>
<!-- 	<!-- 点击更多后产生的的上拉菜单结束 -- -->
  </view>
</template>

<script>
import LoadMore from "../../../components/content/LoadMore.vue";
import ReturnHead from "../../../components/message/ReturnHead.vue";
import PopupBottom from "../../../components/square/PopupBottom.vue";
import navigatorBar from "../../../components/content/NavigatorBar.vue";
import ProfileHead from "../../../components/profile/ProfileHead.vue";
import PrIndexFloor from "../../../components/profile/PrIndexFloor.vue";
import {
  getPost,
  getUserMes,
  setLike,
  setUnLike,
  setNoticeIt,
  setUnnoticeIt,
} from "../../../axios/profile/profile.js";
export default {
  data() {
    return {
      myuid: 1,
      detail: {
        userHeader: "",
        userName: "",
        followNum: 0,
        followedNum: 0,
        likedNum: 0,
        uid: 1,
        motto: "",
      },
      follow: {
        list: [],
      },
      loadStatus: "more", //加载样式：more-加载前样式，loading-加载中样式，nomore-没有数据样式
      isLoadMore: false, //是否加载中
      maxPageNum: 1, //数据库中数据的最大页数(帖子)
      page: 1, //当前加载的页数（帖子）
      loadSize: 20, //一次加载最多20条（帖子）
    };
  },

  components: {
    navigatorBar,
    ProfileHead,
    LoadMore,
    PrIndexFloor,
    PopupBottom,
    ReturnHead,
  },
  methods: {
    //获取用户uid
    getUid() {
	 let _this=this;
     return  new Promise((resolve, reject) => {
        uni.getStorage({
          key: "userInfo",
          success: function (res) {
            let a = res.data;
            _this.myuid = a.uid;
			resolve(res);
            console.log(res.data);
          },
          fail: function (err) {
            console.log("尚未登陆");
			reject(err)
          },
        });
      });
    },
    //点击获取帖子的详情
    forPostDetail(articleId, tagList) {
      let userDetail = { articleId, tagList };
      uni.navigateTo({
        url:
          "../../level2/square/postDetail?userDetail=" +
          JSON.stringify(userDetail),
      });
    },
    //关闭遮罩
    close() {
      this.$refs.popup.close();
    },
    //获取帖子完整的信息包括用户信息
    getPost(uid, page, size, current) {
      return getPost({ uid, page, size, current }).then(
        (res) => {
          console.log(res.data.data);
          //每次请求都要更新当前数据库最大页数
          this.maxPageNum = res.data.data.maxPageNum;
          let postDetailList = [];
          for (let i = 0; i < res.data.data.detail.length; i++) {
            let { articleInfo } = res.data.data.detail[i];
            let { labels } = res.data.data.detail[i];
            let { userInfo, userFollow } = res.data.data.detail[i].userDetail;
            let { userDetail } = res.data.data.detail[i];
            postDetailList.push({
              articleId: articleInfo.articleId,
              uid: articleInfo.uid,
              likedNum: articleInfo.likedNum,
              like: res.data.data.detail[i].like,
              sharedNum: articleInfo.sharedNum,
              commentNum: articleInfo.commentNum,
              userHeader: userInfo.photoPath,
              userName: userInfo.nickname,
              postText: articleInfo.summary,
              postImages: [articleInfo.thumbnailPath || ""],
              tagList: labels,
              postTime:
                new Date(articleInfo.date).getMonth() +
                "/" +
                new Date(articleInfo.date).getDate(),
            });
          }
          this.follow.list.push(...postDetailList);
          this.page++; //当前页数加一
        },
        (err) => {
          console.log(err);
        }
      );
    },
    //取消点赞
    unlikeIt(articleId) {
      setUnLike({ articleId });
      console.log("取消点赞");
    },
    // 点赞
    likeIt(articleId) {
      setLike({ articleId: articleId }).then(
        (res) => [console.log(res)],
        (err) => {
          console.log(err);
        }
      );
      console.log("点赞成功");
    },
    //用户自己信息
    getUserMes(myuid) {
      return getUserMes(myuid).then(
        (res) => {
          // console.log(myuid)
          this.detail.userHeader = res.data.data.photoPath;
          this.detail.userName = res.data.data.nickname;
          this.detail.likedNum = res.data.data.likedNum;
          this.detail.followNum = res.data.data.followNum;
          this.detail.followedNum = res.data.data.followedNum;
          this.detail.motto = res.data.data.motto;
          this.detail.uid = this.myuid;
          console.log(this.detail.motto);
        },
        (err) => {
          console.log(err);
        }
      );
    },
    //上拉触底函数
    onReachBottom() {
      if (this.maxPageNum < this.page) {
        this.loadStatus = "nomore";
        return;
      }
      if (!this.isLoadMore) {
        //此处判断，上锁，防止重复请求
        this.isLoadMore = true;
        console.log("上拉加载中");
        this.getPost(
          [], //登陆用户的还没做好
          this.page,
          this.loadSize //每次拉去20条
        );
        console.log("加载的页数", this.page);
      }
      this.isLoadMore = false;
    },
    // 下拉刷新
    onPullDownRefresh() {
      this.follow.list = [];
      this.page = 1;
      console.log("下拉刷新");
      this.getPost(
        // this.follow,
        [this.myuid],
        this.page,
        this.loadSize,
        null
      ).then(
        (res) => {
          uni.stopPullDownRefresh(); //停止当前页面下拉刷新
          uni.showToast({ title: "刷新成功", icon: "none" });
        },
        (err) => {
          uni.stopPullDownRefresh(); //停止当前页面下拉刷新
          uni.showToast({ title: "出了点小状况", icon: "none" });
        }
      );
    },
  },
  mounted() {
    //初次挂载页面就要加载一定的数据(关注)
    this.getUid().then(res=>{/*cai 这里是异步请求*/
		 this.getPost([this.myuid], this.page, this.loadSize, null);
    	this.getUserMes(myuid);
	}).catch(err=>{
		console.log("快去登陆吧");
	});
   
  },
};
</script>

<style scoped>
.scrollY {
  margin-top: 10px;
  padding-top: 3px;
  height: 94vh;
}
::-webkit-scrollbar {
  width: 0;
  height: 0;
  background-color: transparent;
}
.attention {
  font-size: 17px;
}
</style>
